					this.$session.set('company_key',  response.data.clm_company_key);
					this.$session.set('user_sabun', response.data.clm_user_sabun);
					this.$session.set('material_id', '');
					location.reload();
				}else{
					Swal.fire({
						title:'ID와 패스워드를 확인해주시기 바랍니다.',
						icon: 'error'
					});
