		getToday() {
			let date = new Date()
			let year = date.getFullYear()
			this.clm_this_year_items = [
				{ text:"전체", value:'' },
				{ text: year-2 + "년", value: year-2 },
				{ text: year-1 + "년", value: year-1 },
				{ text: year + "년", value: year },
			]
			if(String(date.getMonth+1) == '1') {
				this.schVal1 = year-1;
			}else {
				this.schVal1 = year;
			}
